import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    Redirect
} from "react-router-dom";

export default class Bill extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            bills : [],
            search: "",
            status: false
        }
    }

    componentDidMount() {
        this.loadBill()
        console.log("1111")
    }

    loadBill = async () => {
        var myHeaders = new Headers();
        myHeaders.append("Authorization", "Bearer "+localStorage.getItem("accessToken"));
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            "search": this.state.search
        });

        var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        body: raw,
        redirect: 'follow'
        };

        try {
            let response = await fetch("https://api-curnon-springbooot.herokuapp.com/api/admin/bill/search", requestOptions)
            if (response.ok) {
                let result = await response.json()
                console.log(result)
                let { data } = result
                this.setState({
                    bills: data
                })
            }
        } catch (error) {
            console.log('error', error)
        }
    }

    selectedId = (id) => {
        this.setState({
            selectedId: id
        })
    }

    reset = () => {
        this.setState({
            bills: [],
            search: ""
        }, this.loadBill)
    }

    deleleBill = async (id) => {
        if (window.confirm('Bn c chc chn mun xa khng ?')) {
            console.log("delete "+id)
            var myHeaders = new Headers();
            myHeaders.append("Authorization", "Bearer "+localStorage.getItem("accessToken"));
    
            var requestOptions = {
                method: 'DELETE',
                headers: myHeaders,
                redirect: 'follow'
            };
    
            try {
                let response = await fetch("https://api-curnon-springbooot.herokuapp.com/api/admin/bill/"+id, requestOptions)
                if(response.ok) {
                    console.log("delete successful")
                    this.reset()
                }
            } catch (error) {
                console.log("error "+error)
            }
        } else {
            // Do nothing!
            console.log('khng xa.');
        }
        
    }

    changeStatus = async (e) => {
        const id = e.target.parentElement.children[0].id
        const value = e.target.parentElement.children[0].value
        var myHeaders = new Headers();
        myHeaders.append("Authorization", "Bearer "+localStorage.getItem("accessToken"));
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            "status": value
        });

        var requestOptions = {
            method: 'PUT',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
        };

        try {
            let response = await fetch("https://api-curnon-springbooot.herokuapp.com/api/member/bill/"+id, requestOptions)
            if(response.ok) {
                console.log(response)
                this.reset()
            }
        } catch (error) {
            console.log('error',error)
        }
        
    }

    render() {
        return (
            <>  
                <div className="page-title">
                    <div className="title_left">
                        <h3>DANH SCH HA N</h3>
                    </div>

                </div>

                <div className="clearfix"></div>

                <div className="row" >
                    <div className="col-md-12 col-sm-12 ">
                        <div className="x_panel">
                            <div className="x_title">
                                <div className="clearfix"></div>
                            </div>
                            <div className="x_content">
                                <div className="row">
                                    <div className="col-sm-12">
                                        <div className="card-box table-responsive">
                                            <TableBill data={this.state.bills} changeStatus={this.changeStatus} selectedId={this.selectedId} deleteId={this.deleleBill}/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

class TableBill extends React.Component {
    render() {
        return <table className="table table-striped table-bordered">
            <thead>
                <tr>
                    <td>Id</td>
                    <td>Ngi mua</td>
                    <td>Ngy mua</td>
                    <td>Tng gi</td>
                    <td>M gim gi</td>
                    <td>Phn trm</td>
                    <td>Trng thi</td>
                    <td>Thanh ton</td>
                    <td>La chn</td>
                    <td>Trng thi</td>
                </tr>
            </thead>
            <tbody>
            {
                this.props.data.map(item => {
                    return <tr key={item.id}>
                        <td>{item.id}</td>
                        <td>{item.userDTO.name}</td>
                        <td>{item.buyDate}</td>
                        <td>{item.priceTotal}</td>
                        <td>{item.coupon}</td>
                        <td>{item.couponPresent}</td>
                        <td>{item.status}</td>
                        <td>{item.pay}</td>
                        <td>
                            <button onClick={() => this.props.deleteId(item.id)}>Xa</button>
                            <button><Link to={"/admin/bill/detail/"+item.id}>Chi tit</Link></button>
                        </td>
                        <td>
                            <select id={item.id} value={item.status} onChange={this.props.changeStatus}>
                                <option value='Cha x l'>Cha x l</option>
                                <option value=' x l'> x l</option>
                            </select>
                        </td>
                    </tr>
                })
            }
            </tbody>
        </table>
    }
}


class SearchBill extends React.Component {
    render() {
        return <>
            <div className="title_right">
                <div className="col-md-5 col-sm-5 col-xs-12 form-group pull-right top_search">
                    <form className="input-group">
                        <input type="text" name="search" className="form-control" placeholder="Tm kim" onChange={this.props.search}/>
                        {/* <span className="input-group-btn">
                            <button className="btn btn-secondary" type="submit">Tm kim</button>
                        </span> */}
                    </form>
                </div>
            </div>
        </>
    }
} 